.skip-to-content {
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #100f0f34;
  border-radius: 50px;
  color: #ffffff;
  padding: 10px;
  z-index: 1000;
  text-decoration: none;
  display: flex;
  align-items: center;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease-in-out;
}

.skip-to-content:hover {
  background-color: #333;
}

.skip-to-content:focus {
  opacity: 1;
