 /* Detail Page */
 .hero-image {
  position: relative;
  overflow: hidden;
}

.restaurant-poster {
  width: 100%;
  height: auto;
}

.restaurant-name {
  font-size: 2.5rem; 
  color: #54473a;
  margin-top: 10px;
  text-align: center;
}

.detailpage-ratings {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 10px;
  font-size: 1.6rem;
}

.detailpage-ratings i {
  color: #f1e35d;
  text-shadow:  0px 0px 10px  #daff0a97;
