}

.restaurant__foodmenu ul,.restaurant__drinkmenu ul {
  list-style-type: none;
  padding: 0;
  margin-bottom: 15px;
}

/* Styles for the review form */
#reviewForm {
  max-width: 400px;
  margin: 20px auto;
  padding: 20px;
  background-color: #f8f8f8;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

#reviewForm label {
  display: block;
  margin-bottom: 8px;
  font-weight: bold;
  color: #333;
}

#reviewForm input,
#reviewForm textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 16px;
  border: 1px solid #d7b99b94;
  border-radius: 4px;
  box-sizing: border-box;
  font-size: 16px;
}

#reviewForm button {
  background-color: #99704F;
  color: #fff;
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s;
}

#reviewForm button:hover {
  background-color: #6a4e37;
}

/* Style for the restaurant reviews */
.restaurant__reviews {
  margin-top: 20px;
  margin-left: 20px; 
  margin-right: 20px; 
}

.restaurant__reviews h3 {
  color: #54473a;
  text-shadow:  0px 0px 15px  #ffffff97;
  font-size: 1.8rem;
  margin-bottom: 15px;
  text-align: center;
}

.restaurant__reviews ul {
  list-style-type: none;
  padding: 0;
}

.restaurant__reviews li {
  background-color: #F2EFE9;
  border-radius: 10px;
  margin-bottom: 15px;
  padding: 15px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.restaurant__reviews li strong {
  color: #5c442d;
}

.restaurant__reviews li p {
  color: #1b1919;
  margin-top: 5px;
}
  
/* Like */
.like {
width: 55px;
height: 55px;
display: flex;
align-items: center;
justify-content: center;
background-color: #A27953;

position: fixed;
bottom: 16px;
right: 16px;
border-radius: 50%;
border: 0;

font-size: 18px;
color: white;
cursor: pointer;
}


/* About Section */
.about{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  gap: 2rem;
}

.about-img img{
  width: 100%;
  height: auto;
}

.about-text h2{
  font-size: 2.2rem;
  line-height: 2.3rem;
  margin-bottom: 2rem;
}

.about-text p{
  color: #fff;
  font-size: 1.2rem;
  line-height: 1.65rem;
  margin-bottom: 1rem;
}

/* Footer */
footer {
  background-color: #533d2b;
  color: white;
  text-align: center;
  padding: 0.8rem;
  position: relative;
  bottom: 0;
  width: 100%;
}

footer a {
  display: inline-block;
  padding: 1.3em;
  text-decoration: none;
  color: #fff;
}

footer a:hover{
  color: #A27953;
  text-shadow: 0px 0px 10px  rgba(255, 255, 255, 0.8);
}

