FFFFF;\n  }\n  \n  *::-webkit-scrollbar-track:hover {\n    background-color: #CFCFB9;\n  }\n  \n  *::-webkit-scrollbar-track:active {\n    background-color: #EBEAD1;\n  }\n  \n  *::-webkit-scrollbar-thumb {\n    border-radius: 5px;\n    background-color: #E4C19C;\n  }\n  \n  *::-webkit-scrollbar-thumb:hover {\n    background-color: #AD9377;\n  }\n  \n  *::-webkit-scrollbar-thumb:active {\n    background-color: #8D7761;\n  }\n  \n.skip-to-content {\n  position: fixed;\n  top: 20px;\n  left: 50%;\n  transform: translateX(-50%);\n  background-color: #100f0f34;\n  border-radius: 50px;\n  color: #ffffff;\n  padding: 10px;\n  z-index: 1000;\n  text-decoration: none;\n  display: flex;\n  align-items: center;\n  opacity: 0;\n  pointer-events: none;\n  transition: opacity 0.3s ease-in-out;\n}\n\n.skip-to-content:hover {\n  background-color: #333;\n}\n\n.skip-to-content:focus {\n  opacity: 1;\n  pointer-events: auto;\n}\n\n/* ini yang belum aman */\n .hero {\n      position: relative;\n      color: #fff;\n      text-align: center;\n      padding: 0;\n    }\n\n  .hero-content {\n    position: absolute; \n    top: 50%; \n    left: 50%; \n    transform: translate(-50%, -50%);\n    z-index: 1;\n    max-width: 800px;\n    margin: 0 auto;\n  }\n  \n  .hero img {\n    width: 100%; \n    height: auto; \n    display: block;\n    aspect-ratio: 4 / 2.4;\n  }\n  \n  .hero h1 {\n    font-size: 2.5rem;\n    margin-bottom: 20px;\n    color: #e8e8e7;\n    text-shadow: 2px 2px 3px rgba(7, 5, 5, 0.6);\n  }\n  \n  .hero p {\n    font-size: 1.2rem;\n    line-height: 1.6;\n    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);\n  }\n  \n  .middle-text{\n    text-align: center;\n    padding-top: 25px;\n    padding-bottom: 0;\n  }\n  \n  .middle-text h2{\n    font-size: 3rem;\n    font-weight: 700;\n    margin-bottom: 10px;\n    color: #7D6A56;\n  }\n  \n  .middle-text p{\n    font-size: 1.4rem;\n    margin-bottom: 10px;\n    color: #ffffff;\n  }\n  \n  .restaurant-content {\n    display: grid;\n    grid-template-columns: repeat(auto-fit, minmax(300px, auto));\n    gap: 2rem;\n    align-items: center;\n    margin-top: 4rem;\n    padding: 2rem;\n  }\n\n  .restaurant-item__not__found {\n    text-align: center;\n    font-size: 1.5rem;\n    margin-bottom: 5px;\n    font-weight: 500;\n    color: #5c442d;\n  }\n  \n  .row {\n    position: relative;\n    padding: 10px 30px 40px;\n    background-color: #d5cfc98e;\n    border-radius: 3rem;\n    text-align: center;\n    transition: all .50s ease;\n    height: 440px;\n    overflow: hidden;\n  }\n  \n  .row:hover{\n    transform: scale(1.1);\n    box-shadow: 7px 8px 4px -3px rgba(14, 0, 0, 0.67);\n  }\n  \n  .row img {\n    width: 300px; \n    height: 210px;\n    transition: transform .50s ease;\n    cursor: pointer;\n    border-radius: 2.3rem;\n    margin-top: 1.5rem;\n    aspect-ratio: 3 / 2;\n  }\n  \n  .row h3{\n  font-size: 1.5rem;\n  margin-bottom: 5px;\n  font-weight: 500;\n  color: #5c442d;\n  }\n  \n  .row p{\n    color: #ffffff;\n    font-size: 1.2rem;\n    line-height: 30px;\n    margin-bottom: 1.1rem\n  }\n  \n  .detail-ratings .s-btnn{\n    display: flex;\n  }\n  \n  .detail-ratings i{\n    color: #f1e35d;\n    text-shadow:  0px 0px 5px  #daff0a97;;\n    font: 1.2rem;\n    font-family: 'Poppins', sans-serif;\n    margin-right: 5px;\n    margin-bottom: 12px;\n  }\n  \n  .about-btnn a{\n    display: inline-block;\n    padding: 9px 23px;\n    background-color: #e7e0e0f7;\n    color: rgb(0, 0, 0);\n    font-size: 14px;\n    font-weight: 500;\n    letter-spacing: 1px;\n    border-radius: 3rem;\n    transition: all .50s ease;\n  }\n  \n  .about-btnn a:hover{\n    background-color: #ffffffdf;\n    color: #000000;\n    box-shadow: 0px 0px 10px 3px rgba(255, 255, 255, 0.737);\n  }\n\n  .s-btnn a{\n    display: inline-block;\n    padding: 9px 23px;\n    background-color: #e7e0e0f7;\n    color: rgb(0, 0, 0);\n    font-size: 14px;\n    font-weight: 500;\n    letter-spacing: 1px;\n    border-radius: 3rem;\n    transition: all 